import React from "react";
import Col from "react-bootstrap/Col";
import SkillsBar from "./SkillsBar";

function SkillsSection({ skills, isScrolled, hideFirstLabel }) {
  const map = new Map();
  skills.forEach((item) => {
       const key = item.category;
       const collection = map.get(key);
       if (!collection) {
           map.set(key, [item]);
       } else {
           collection.push(item);
       }
  });
  return (
    <>
      {
        [...map].map(([key, value], index) => 
          {
            return( 
              <div key={index}>
                {(!hideFirstLabel || index !== 0) ? (<h1 className="display-6 mb-1 mt-3" style={{ width: "95%" }}>{key}</h1>) : null}
                {
                  value.map((skill, idx) => (
                    <SkillsBar
                      key={`${skill}-${index}-${idx}`}
                      skill={skill.name}
                      value={skill.value}
                      isScrolled={isScrolled}
                    />
                  ))
                }
              </div>
            ) 
          }
        )      
      }
    </>
  );
}

function SkillsTab({ skills, isScrolled }) {
  return (
    <>
      <Col xs={12} md={6}>
        <SkillsSection
          skills={skills.slice(0, Math.floor(skills.length / 2))}
          isScrolled={isScrolled}
        />
      </Col>
      <Col xs={12} md={6}>
        <SkillsSection
          skills={skills.slice(Math.floor(skills.length / 2), skills.length)}
          isScrolled={isScrolled}
          hideFirstLabel={true}
        />
      </Col>
    </>
  );
}

export default SkillsTab;
